.container{
	padding: 20px;
}
.table{
	margin-top: -2px;
	text-align: center;
	line-height: 36px;
}
.table tr td, .table tr th{
	border-bottom: 1px solid #d0d0d0;
	border-right: 1px solid #d0d0d0;
	border-top: 1px solid #d0d0d0;
	font-size: 12px;
}
.table tr th{
	background-color: #d0d0d0;
}
table{
	width: 100%;
	border-left: 1px solid #d0d0d0;
}
.table_title{
	text-align: left;
}
.table_title span:first-child{
	margin-right: 45px;
}
.table_page{
	border: 0.1px solid #d0d0d0;
	border-top: none;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	font-size: 12px;
}
.table_page button{
	padding: 0 20px;
  display: inline-block;
  height: 28px;
  margin: 5px 15px;
	border: 0.1px solid #d0d0d0;
	color: #ccc;
	background: none;
	font-size: 12px;
}
.table_page button.active{
	color: #127bc7;
}


