.title_section{
	position: fixed;
	top: 0;
	left: 0;
	z-index: 1100;
	width: 100%;
	height: 34px;
	background-color: #383c40;
}
.title_section > p{
	float: left;
	margin-left: 20px;
	height: 100%;
	line-height: 34px;
	font-size: 14px;
	color: #fefefe;
}
.title_section > button{
  border: none;
  background: no-repeat;
  float: right;
  display: inline-block;
  line-height: 34px;
  margin-right: 12px;
  cursor: pointer;
  color: #fefefe;
}


