.container{
	padding: 18px;
}
.back_btn{
	margin-bottom: 10px;
}
.back_btn button{
	padding: 5px 30px;
	border: none;
	color: #fefefe;
	background-color: #0f5ab0;
	font-size: 14px;
	cursor: pointer;
}
.table{
	margin-top: 8px;
	text-align: center;
	line-height: 36px;
}
.table tr td{
	border-bottom: 1px solid #d0d0d0;
	border-right: 1px solid #d0d0d0;
	font-size: 12px;
}
table{
	width: 100%;
	border-left: 1px solid #d0d0d0;
}
.table_head{
	line-height: 38px;
	padding: 0 15px;
	background-color: #d0d0d0;
	border: 1px solid #d0d0d0;
	text-align: left;
}
.basic_info_table{
	width: 100%;
}
.basic_info_table tr td:nth-child(odd){
	background-color: #e8e8e8;
}
.other_table tr td:first-child{
	width: 150px;
}
.table_page{
	border: 0.1px solid #d0d0d0;
	border-top: none;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	font-size: 12px;
}
.table_page button{
	padding: 0 20px;
  display: inline-block;
  height: 28px;
  margin: 5px 15px;
	border: 0.1px solid #d0d0d0;
	color: #ccc;
	background: none;
	font-size: 12px;
}
.table_page button.active{
	color: #217ce7;
}
.table_head .table_head_title{
	display: inline-block;
	width: 120px;
}
.table_head .message{
	/*margin-left: 80px;*/
}
.table_head > a{
	float: right;
	color: #217ce7;
}
.table_head > button{
  float: right;
  padding: 0;
  background: none;
  border: none;
  color: #217ce7;
  display: inline;
  line-height: 38px;
}

.add_module{
	position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: rgba(0,0,0,0.6);
  z-index: 1200;
}
.add_module > header{
	width: 390px;
  background-color: #fff;
  display: block;
  margin: auto;
  margin-top: 120px;
  text-align: left;
  border: 0.1px solid #d0d0d0;
  padding-left: 10px;
}
.add_module > header button{
	float: right;
	background-color: #c00;
	border: none;
	color: #fff;
	height: 37px;
  width: 36px;
  font-size: 18px;
}
.add_body{
	margin: auto;
	display: block;
	width: 400px;
	background-color: #fff;
	-ms-flex-order: 0.1px solid #d0d0d0;
	    order: 0.1px solid #d0d0d0;
  border-top: none;
}
.add_body textarea{
  width: calc(100% - 30px);
  margin-top: 15px;
  height: 120px;
  overflow: hidden;
  resize: none;
}
.add_body .submit{
  background-color: #127bc7;
  border: 0.1px solid #d0d0d0;
  color: #fefefe;
  width: 370px;
  height: 34px;
  margin-bottom: 10px;
}


