.container_body{
	margin-left: 130px;
	margin-top: 34px;
}

ul{
	position: fixed;
	left: 0;
	top: 34px;
	z-index: 1099;
	height: calc(100% - 34px);
	width: 130px;
	background-color: #0f5ab0;
	font-size: 15px;
}
ul li{
	cursor: pointer;
	margin-top: 3px;
	line-height: 36px;
	text-align: center;
	letter-spacing: 2px;
  color: #fefefe;
}
ul li.active{
	background-color: #d2e8ff;
	color: #000;
}
ul li.active a{
	color: #000;
}
ul li:first-child{
	margin-top: 18px;
}
ul li a{
	color: #fefefe;
}


