.login_container{
 	position: fixed;
  top: 30%;
  width: 100%;
  text-align: center;
  font-size: 14px;
}
.login_container > p{
	font-size: 18px;
	font-weight: bold;
	letter-spacing: 1px;
	margin-bottom: 18px;
}
.login_container > div{
  width: 260px;
  margin-left: calc(50% - 130px);
  border: 1px solid #d0d0d0;
  border-radius: 1px;
  height: 36px;
  line-height: 36px;
  margin-top: 12px;
}
.login_container > div input{
	border: none;
}
.login_container label{
   display: block;
  text-align: left;
  padding-left: 12px;
  line-height: 36px;
  color: #858585;
}
.login_container button{
  border: none;
  margin-top: 12px;
  margin-left: 1px;
  width: 262px;
  font-size: 14px;
  height: 38px;
  color: #fefefe;
	background-color: #0084e0;
}


